import React from "react"
import styled from "styled-components"
import {GithubContext} from "../context/context"
import {Pie3D, Column3D, Bar3D, Doughnut2D} from "./Charts"

const Repos = () => {
	const {repos} = React.useContext(GithubContext)
	// STEP 2 - Chart Data
	// const chartData = [
	//   {
	//     label: "HTML",
	//     value: "5"
	//   },
	//   {
	//     label: "CSS",
	//     value: "8"
	//   },
	//   {
	//     label: "Javascript",
	//     value: "15"
	//   }
	// ];
	const languages = repos.reduce((total, curItem) => {
		const {language, stargazers_count} = curItem
		if (language === null) return total
		if (total[language] === undefined) {
			total[language] = {label: language, value: 1, stars: stargazers_count}
		} else {
			total[language] = {
				...total[language],
				value: total[language].value + 1,
				stars: total[language].stars + stargazers_count,
			}
		}
		return total
	}, {})

	// convert to an array to sort by most used language
	// and only display the top 5
	const mostUsed = Object.values(languages)
		.sort((a, b) => {
			return b.value - a.value
		})
		.slice(0, 5)

	// most stars per language
	const mostPopular = Object.values(languages)
		.sort((a, b) => {
			return b.stars - a.stars
		})
		.map((item) => {
			return {...item, value: item.stars}
		})
		.slice(0, 5)

	// stars, forks
	let {stars, forks} = repos.reduce(
		(total, curItem) => {
			const {stargazers_count, name, forks} = curItem
			total.stars[stargazers_count] = {
				label: name,
				value: stargazers_count,
			}
			total.forks[forks] = {
				label: name,
				value: forks,
			}
			return total
		},
		{
			stars: {},
			forks: {},
		}
	)

	// convert to array and display top 5 stared repos
	stars = Object.values(stars).slice(-5).reverse()
	forks = Object.values(forks).slice(-5).reverse()

	return (
		<section className="section">
			<Wrapper className="section-center">
				<Pie3D data={mostUsed} />
				<Column3D data={stars} />
				<Doughnut2D data={mostPopular} />
				<Bar3D data={forks} />
			</Wrapper>
		</section>
	)
}

const Wrapper = styled.div`
	display: grid;
	justify-items: center;
	gap: 2rem;
	@media (min-width: 800px) {
		grid-template-columns: 1fr 1fr;
	}

	@media (min-width: 1200px) {
		grid-template-columns: 2fr 3fr;
	}

	div {
		width: 100% !important;
	}
	.fusioncharts-container {
		width: 100% !important;
	}
	svg {
		width: 100% !important;
		border-radius: var(--radius) !important;
	}
`

export default Repos
