import React from "react"
import {GithubContext} from "../context/context"
import styled from "styled-components"
import {MdBusiness, MdLocationOn, MdLink, MdLaptopMac} from "react-icons/md"
import {FaTwitter} from "react-icons/fa"
import { RiUserFollowLine } from "react-icons/ri"
const Card = () => {
	const {githubUser} = React.useContext(GithubContext)
	const {
		avatar_url,
		html_url,
		name,
		company,
		blog,
		bio,
		location,
		twitter_username,
		hireable,
		login,
	} = githubUser

	return (
		<Wrapper>
			<header>
				<img src={avatar_url} alt={name} />
				<div>
					<h4>{name ? name : login} </h4>
					{twitter_username
						? twitter_username && (
								<a href={`https://www.twitter.com/${twitter_username}`}>
									<FaTwitter></FaTwitter>
									{twitter_username}
								</a>
						  )
						: null}
				</div>
				<a href={html_url}> <RiUserFollowLine></RiUserFollowLine> follow</a>
			</header>
			<p className="bio">{bio}</p>
			<div className="links">
				{hireable
					? hireable && (
							<p>
								<MdLaptopMac></MdLaptopMac>Looking for work
							</p>
					  )
					: null}
				{company
					? company && (
							<p>
								<MdBusiness></MdBusiness> {company}
							</p>
					  )
					: null}
				{location
					? location && (
							<p>
								<MdLocationOn></MdLocationOn> {location}
							</p>
					  )
					: null}
				{blog
					? blog && (
							<a href={blog}>
								<MdLink></MdLink>
								{blog}
							</a>
					  )
					: null}
			</div>
		</Wrapper>
	)
}
const Wrapper = styled.article`
	background: var(--clr-white);
	padding: 1.5rem 2rem;
	border-top-right-radius: var(--radius);
	border-bottom-left-radius: var(--radius);
	border-bottom-right-radius: var(--radius);
	position: relative;
	&::before {
		content: "user";
		position: absolute;
		top: 0;
		left: 0;
		transform: translateY(-100%);
		background: var(--clr-white);
		color: var(--clr-grey-5);
		border-top-right-radius: var(--radius);
		border-top-left-radius: var(--radius);
		text-transform: capitalize;
		padding: 0.5rem 1rem 0 1rem;
		letter-spacing: var(--spacing);
		font-size: 1rem;
	}
	header {
		display: grid;
		grid-template-columns: auto 1fr auto;
		align-items: center;
		column-gap: 1rem;
		margin-bottom: 1rem;
		img {
			width: 75px;
			height: 75px;
			border-radius: 50%;
		}
		h4 {
			margin-bottom: 0.25rem;
		}
		p {
			margin-bottom: 0;
		}
		a {
			color: var(--clr-primary-5);
			border: 1px solid var(--clr-primary-5);
			padding: 0.25rem 0.75rem;
			border-radius: 1rem;
			text-transform: capitalize;
			letter-spacing: var(--spacing);
			transition: var(--transition);
			cursor: pointer;
			&:hover {
				background: var(--clr-primary-5);
				color: var(--clr-white);
			}
		}
	}
	.bio {
		color: var(--clr-grey-3);
		font-style: italic;
	}
	.links {
		p,
		a {
			margin-bottom: 0.25rem;
			display: flex;
			align-items: center;
			svg {
				margin-right: 0.5rem;
				font-size: 1.3rem;
			}
		}
		a {
			color: var(--clr-primary-5);
			transition: var(--transition);
			svg {
				color: var(--clr-grey-5);
			}
			&:hover {
				color: var(--clr-primary-3);
			}
		}
	}
`
export default Card
